import { h } from "preact";
import { useEffect } from "preact/compat";
import { useObjectState } from "./useObjectState";
import register from "./preact-custom-element";
import { withForm } from "./utils/withForm";

export function FormContainer({ nativeElement }) {
  const [getForm, setForm] = useObjectState(nativeElement.state || {});
  nativeElement.state = nativeElement.state || {};

  useEffect(() => {
    Object.assign(nativeElement, {
      __bootstrapped: true,
      update(payload) {
        setForm(payload);
      }
    });
    nativeElement.addEventListener("input", event => {
      const { name, value } = event.target;
      setForm({ [name]: value });
    });
    nativeElement.addEventListener("submit", event => {
      console.log("submit", event);
    });
    setForm(nativeElement.state)
  }, []);

  useEffect(() => {
    nativeElement.state = getForm();
    nativeElement.dispatchEvent(
      new CustomEvent("formupdate", { detail: getForm() })
    );
  }, [getForm()]);

  return null;
}

register(FormContainer, "form-container", []);
